import React, { Component } from 'react'
import './../css/dragonPark.css';
import Footer from './Footer';
import {Link} from "react-router-dom";
import Nav from './Nav';
import axios from 'axios';
const getProductData = () =>  axios.get('/ds_News')
                                   .then((res) => res.data);                           
export default class DragonPark extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data: [],
            numberInit: 1,
            numberEnd: 8,
            flagHandle1 : true,
            flagHandle2 : false,
        }
    }

    componentDidMount() {
        getProductData().then((res) =>{
            console.log(res.recordset);
            this.setState({
                data: res.recordset
            })
          })
    }
    handleClick1 = () => {
        if(this.state.flagHandle2 === true)
        {
            this.setState({
                flagHandle1 : true,
                flagHandle2 : false,
                numberInit : 1,
                numberEnd : 8
            })
        }
    }
    handleClick2 = () => {
        if(this.state.flagHandle1 === true)
        {
            this.setState({
                flagHandle1 : false,
                flagHandle2 : true,
                numberInit : 9,
                numberEnd : 2*8
            })
        }
    }
    render() {
        return (
            <div>
                <Nav />
                <section className="main">
                    <div className="container">
                        <div className="main-top text-center mt-4">
                            <h3 className="mb-5">News - SANJALIKA WATER PARK</h3>
                        </div>
                        <div className="row">
                            {this.state.data.map((val, key) => {
                                if (key+1 >= this.state.numberInit && key+1 <= this.state.numberEnd) {
                                    return (
                                        <div className="col-sm-3 box-news mt-4">
                                            <img className="img-fluid box-img" src={val.Image} alt="" />
                                            <div className="box-info">
                                                <Link to={'/NewsDetail/'+ val.ID} className="box-info-title">{val.Title}</Link>
                                                <p>News | <i className="far fa-calendar-alt mr-2 ml-2" />{val.Date}</p>
                                                <p>{val.Content}</p>
                                                <Link to={'/NewsDetail/'+ val.ID} className="more"> See More &gt;</Link>
                                            </div>
                                        </div>)
                                }
                                else
                                {
                                    return '';
                                }
                            })}
                        </div>
                        <div className="handleButton mt-5">
                            <div className="handleButton-one" onClick={() =>{this.handleClick1()}}>1</div>
                            <div className="handleButton-two" onClick={() =>{this.handleClick2()}}>2</div>
                        </div>
                    </div>
                </section>
                <Footer />
            </div>
        )
    }
}